<template>
  <div ref="scroll">
    <div class="scroll-content">
      <slot></slot>
    </div>
  </div>
</template>


<script>
import BScroll from 'better-scroll'

// ref如果是绑定在组件中的，那么通过this.$refs.refname获取到的是一个组件对象
// ref如果是绑定在普通的元素中，那么通过this.$refs.refname获取到的是一个元素对象

export default {
  name:'Scroll',
  data() {
    return {
      scroll:null,
      
    }
  },
  props:{
    probeType:{
      type:Number,
      default:0
    },
    pullUpLoad:{
      type:Boolean,
      default:false
    }
  },
  components: {
    
  },
  activated() {
  },
  watch: {
  },
  created(){
  },
  mounted(){
    // probeType:0/1/2(手指滚动)/3(惯性滚动)    监听滚动
    this.scroll = new BScroll(this.$refs.scroll,{
      probeType: this.probeType,
      pullUpLoad: this.pullUpLoad,
      mouseWheel: true,
      click:true,
    })
    //监听滚动位置
    this.scroll.on('scroll',(position) => {
      this.$emit('scroll',position)
    })
    //监听scroll滚动到底部  监听上拉事件
    if(this.pullUpLoad){
      this.scroll.on('pullingUp',() => {
        this.$emit('pullingUp')
      })

    }
    
    // 

  },
  methods:{
    scrollTo(x,y,time=500){
      this.scroll && this.scroll.scrollTo(x,y,time) //逻辑语 && 与 前面为null啧后面不执行
    },
    finishPullUp(){
      this.scroll.finishPullUp()
    },
    refresh(){
      this.scroll && this.scroll.refresh()//重新计算scroll高度
      // console.log('---');
    }
    
  }

}
</script>

