<template>
  <div class="goods-item" @click="itemClick">
    <img :src="goodsItem.show.img" alt="图片" @load="goodslImageLoad">
    <div class="goods-info">
      <p>{{goodsItem.title}}</p>
      <span class="price">￥{{goodsItem.price}}</span>
      <span class="cfav">{{goodsItem.cfav}}</span>
    </div>
  </div>
</template>

<script>
export default {
  name:'',
  data() {
    return {
    }
  },
  props:{
    goodsItem:{
      type:Object,
      default(){
        return {}
      }
    }
  },
  components: {
    
  },
  activated() {
  },
  watch: {
  },
  created(){
  },
  mounted(){
  },
  methods:{
    goodslImageLoad(){
      this.$bus.$emit("goodslImageLoad") //事件总线  用于非父子组件通信
    },
    itemClick(){
      this.$router.push('/detail/'+ this.goodsItem.iid)
    }
  }

}
</script>

<style>
.goods-item{
  width: 48%;
  position: relative;
  padding-bottom: 40px;
  margin-bottom: 5px;
}
.goods-item img{width: 100%;border-radius: 5px;}
.goods-info{
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 4px;
  text-align: center;
}
.goods-info p{
  font-size: 14px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
  .goods-info .price{
    color: var(--color-high-text);
    font-size: 12px;
  }
  .goods-info .cfav{
    position: relative;
    padding-left: 12px;
    font-size: 12px;
  }
  .goods-info .cfav::after{
    content: "\2605";
    position: absolute;
    top: 1px;left: 2px;
  }
  .goods-info .cfav.act::after{
    color: var(--color-high-text);
  }
</style>
