<template>
  <div class="tabControl">
    <div class="tabControlItem" 
    :key="index" 
    v-for="(item,index) in tabControl"
    :class="{ active: index === istabControlIndex }"
    @click="tabControlItem(index)"
    >
      <span>{{item}}</span>
    </div>
  </div>
</template>

<script>
export default {
  name:'',
  data() {
    return {
      istabControlIndex:0,
    }
  },
  components: {
    
  },
  props:{
    tabControl:{
      type: Array,
      default() {
        return []
      }
    }
  },
  activated() {
  },
  watch: {
  },
  created(){
  },
  mounted(){
  },
  methods:{
    tabControlItem(index){
      this.istabControlIndex=index
      this.$emit('tabClick',index)//子传父事件，自定义事件
    }
  }

}
</script>

<style>
  .tabControl{
    display: flex;
    font-size: 14px;
    padding: 10px 0;
    padding-bottom: 14px;
    text-align: center;
  }
  .tabControlItem{
    flex: 1;
  }
  .tabControlItem span{
    padding: 3px;
  }
  .active{color: var(--color-high-text);}
  .active span{border-bottom: 2px solid var(--color-high-text);}
</style>
