<template>
  <div id="home">
    <nav-bar class="nav-bar">
      <div slot="center">购物街</div>
    </nav-bar>
    <tab-control 
        :tabControl="['流行','新款','精选']" 
        class="tab-control" 
        @tabClick="tabClick" 
        ref="tabControl1" 
        v-show="isTabFixed"/>

    <scroll class="scroll" 
    ref="scroll" 
    :probe-type="3" 
    :pull-up-load="true"
    @scroll="contentScroll"
    @pullingUp="contentScrollUp"
    >
      <home-swiper :banners="banners" @SwiperImgLoad="SwiperImgLoad"/>
      <recommend-view :recommend="recommend" />
      <feature-view :recommend="recommend"/>
      <tab-control 
        :tabControl="['流行','新款','精选']" 
        @tabClick="tabClick" 
        ref="tabControl2" />
      <goods-list :goods="goods[currentType].list" ref="goods-list" />
    </scroll>

    <backTop @click.native="backTopClick" v-show=isShowBackTop /> <!-- .native监听组件元素的根元素组件 -->
   
  </div>
</template>

<script>
  //自己的子组件
  import homeSwiper from './childComps/homeSwiper';import {iniSwiper} from 'components/common/swiper'
  import recommendView from './childComps/recommendView'
  import featureView from './childComps/featureView'

  //公共组件
  import NavBar from 'components/common/navbar/NavBar'
  import tabControl from 'components/content/tabControl/tabControl'
  import goodsList from 'components/content/goods/goodsList'
  import scroll from 'components/common/scroll/Scroll'
  import backTop from 'components/content/backTop/backTop'

  //插件，框架 引用
  import {getHomeMultidata , getHomeGoods} from 'network/home'//Ajax网络请求
  



  // import { Swiper, SwiperSlide } from 'swiper/vue';
  // import 'swiper/swiper-bundle.css';

  export default{
    name:"home",
    components: {
      homeSwiper,iniSwiper,
      recommendView,
      featureView,

      NavBar,
      tabControl,
      goodsList,
      scroll,
      backTop,

    },
    data(){
      return{
        banners:[],
        recommend:[],
        goods:{//商品数据
          'pop':{page:0, list:[]},
          'new':{page:0, list:[]},
          'sell':{page:0, list:[]},
        },
        currentType: 'pop',
        isShowBackTop: false,
        tabOffsetTop:0,
        isTabFixed: false,
        saveY:0,
      }
    },
    created() {  //vue实例创建完执行
      this.getHomeMultidata()

      //请求商品数据
      this.getHomeGoods('pop')  //流行
      this.getHomeGoods('new')  //新款
      this.getHomeGoods('sell') //精选

      

    },
    mounted(){
      //监听item中图片加载完成
      const refresh= this.debounce(this.$refs.scroll.refresh,50)

      this.$bus.$on('goodslImageLoad',() => {
        // this.$refs.scroll.refresh()//重新计算scroll高度 
        refresh()
      })

      //获取tabControl的offsetTop
      //所有的组件都有一个属性$el，用于获取组件中的元素
      
    },
    methods:{

      //事件监听
      tabClick(index){  //分类点击
        switch(index){
          case 0:
            this.currentType = 'pop'
            break
          case 1:
            this.currentType = 'new'
            break
          case 2:
            this.currentType = 'sell'
            break
        }
        this.$refs.tabControl1.istabControlIndex = index;
        this.$refs.tabControl2.istabControlIndex = index;
      },

      debounce(func,delay){  //防抖动函数
        let timer = null
        return function(...args){
          if(timer) clearTimeout(timer)
          timer = setTimeout(() =>{
            func.apply(this,args)
          },delay)
        }
      },
      //返回顶部
      backTopClick(){
        this.$refs.scroll.scrollTo(0,0,500)
      },

      //返回顶部是否显示
      contentScroll(position){
        this.isShowBackTop = position.y < -800 ? true : false

        //决定tabconter是否吸顶(position：fixed)
        this.isTabFixed = (-position.y + 44) > this.tabOffsetTop
      },

      //上拉加载更多
      contentScrollUp(){
        this.getHomeGoods(this.currentType)
      },

      //banner图片加载完，吸顶效果
      SwiperImgLoad(){
        this.tabOffsetTop = this.$refs.tabControl2.$el.offsetTop
      },
      //goodsList所有图片加载完成时执行
      // goodslImageLoad(){
      //   this.$refs.scroll.scroll.refresh()//重新计算scroll高度
      // },


      //网络请求
      getHomeMultidata(){
        getHomeMultidata().then(res =>{
        this.banners = res.data.banner.list;//获取banner列表
        this.recommend = res.data.recommend.list;//获取recommend列表
        this.banners.length > 1 ? this.$nextTick(() => iniSwiper()) : console.log('只有一个图片不轮播');//Swiper初始化
        this.$nextTick(() => this.$refs.scroll.scroll.refresh())//Scroll初始化
        // console.log(res)
      }).catch(err => {
        console.log(err)
      })
      },

      getHomeGoods(type){
        let page = this.goods[type].page + 1
        getHomeGoods(type,page).then(res => {
          this.goods[type].list.push(...res.data.list) //...数组解构，依次push进去
          this.goods[type].page += 1
          
          this.$refs.scroll.finishPullUp()
        })
      },

      
      
    },

    activated(){
      this.$refs.scroll.scrollTo(0,this.saveY,0)
      this.$refs.scroll.refresh()
    },
    deactivated(){
      this.saveY = this.$refs.scroll.scroll.y
      // console.log(this.$refs.scroll.scroll.y);
    },
    
  }
</script>

<style scoped>
  #home{
    height: 100vh;
  }
  .nav-bar{
    background-color: var(--color-high-text);
    color: #fff;
    border-bottom: none;
  }
  .tab-control{
    position: fixed;
    top: 44px;
    left: 0;right: 0;
    background-color: #fff;
    z-index: 99;
  }
  .scroll{
    height: calc(100% - 93px);
    overflow: hidden;
    /* position: relative;
    top: 44px; */
  }
</style>
